

























































































import { Vue, Component } from 'vue-property-decorator'
import Http, { URL_TRADE } from '../../service'

@Component
export default class Trade extends Vue {
  data: any = null

  created () {
    this.updateData()
  }

  updateData () {
    Http.get(URL_TRADE, {}).then((response) => {
      this.data = response.extra
    })
  }
}
